/* Home.css */

.container {
  display: flex;
  justify-content: center;
  height: 100%;
}
.loader {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  font-weight: 300;
}
.movies {
  display: grid;
  grid-template-columns: repeat(2, minmax(400px, 1fr));
  grid-gap: 100px;
  width: 80%;
  padding: 70px 50px 50px 50px;
}

@media screen and (max-width: 767px) {
  .movies {
    grid-template-columns: 1fr;
    width: 100%;
  }
}